	// 自定义gameItem组件
	import gameItem from '@/components/k-ui/k-game-list/k-game-attention.vue';
	// import kEmpty from '@/components/k-empty/k-empty.vue';
	export default {
		components: {
			gameItem,
			// kEmpty
		},
		data() {
			return {
				datas: [],
			}
		},
		onLoad(options){
			uni.showLoading({
				title: '正在加载'
			})
			this.getDatas(options.id)
		},
		watch:{
			// 监控数据名为datas的属性,显示empty界面
			// datas(e){
			// 	// console.log(e)
			// 	let empty = e.length === 0 ? true : false
			// 	if(this.empty !== empty){
			// 		this.empty = empty
			// 	}
				
			// }
		},
		methods:{
			async getDatas(id) {
				const res = await this.$api.request({
					url: '/game/attentionList?id=' + id
				})
				console.log(res)
				this.datas = res.data
				uni.hideLoading()
		},
		// 跳转到比赛详情
		goGameDetail(id) {
			uni.navigateTo({
				url: '/pages/game/game-detail?id=' + id
			})
		},
	}
}
